            </div><div class="chengy clearfix">
              <p class="ipt_1">
                联系电话
              </p>
              <p class="inpt_2">
                <input type="text" class="input1"/>
              </p>
            </div><div class="chengy clearfix">
              <p class="ipt_1">
                <span>*</span>目的城市
              </p>
              <p class="inpt_2">
              <select>
                <option value="">省/市/区</option>
                <option value="">1</option>
                <option value="">2</option>
              </select>
              <input type="text" placeholder="请输入详细地址" class="input2"/>
              </p>
            </div><div class="chengy clearfix">
              <p class="ipt_1">
