<script>
import wdcd from '@/components/WDCD-left'
export default {
  components: {
    wdcd
  },
  data() {
    return {
      checked: false,
      checkeda: false,
      checkeds: false,
      checkedd: false,
      checkedf: false,
      formInline: {
        user: '',
        region: ''

      }
    }
  },
  methods: {
    onSubmit() {
      console.log('submit!');
    }
  }
}
