                border: 1px solid #7c93c5;
                overflow: hidden;
                background: #fafbff;
                .input1 {
                    width: 220px;
                    height: 40px;
                    float: left;
                }
                .input2 {
                    border-left: 1px solid #7c93c5;
                    width: 120px;
                    height: 40px;
                    float: left;
                }
            }
            .ipt_3 {
                color: orangered;
                width: 120px;
                height: 40px;
                text-align: center;
                line-height: 40px;
                float: left;
            }
            .inpt_2 {
