                border: 1px solid #DADEEE;
                overflow: hidden;
                background: #fafbff;
                line-height: 40px;
            }
            .actives {
                width: 1280px;
                margin: 0 auto;
                height: 500px;
                padding: 30px 0;
            }
            .chengy {
                margin-top: 20px;
            }
            .inptn_2 {
                width: 367px;
                height: 40px;
                float: left;
            }
        }
        .bottom-from-right {
