            float: right;
            border: 1px solid #F5EFEF;
            background: #FFFCFD;
            padding-bottom: 300px;
            .bottom-from-right-t {
                font-size: 14px;
                font-weight: 700;
                padding: 20px;
                border-bottom: 1px solid #F5EFEF;
            }
            ul {
                padding: 10px 20px;
                li {
                    border: 1px solid #F5EFEF;
                    padding: 10px;
                    margin: 10px 0;
                    span {
                        width: 70px;
                        float: left;

                    }
                    .el-icon-minus {
                        color: orangered;
                    }
                }
            }
            p {
                padding: 0 20px;
                color: #ccc;
            }
        }
